import {useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import usePolyDogeFinance from './usePolyDogeFinance';
import useRefresh from './useRefresh';

const useTotalStakedOnBoardroom = () => {
  const [totalStaked, setTotalStaked] = useState(BigNumber.from(0));
  const polydogeFinance = usePolyDogeFinance();
  const {slowRefresh} = useRefresh();
  const isUnlocked = polydogeFinance?.isUnlocked;

  useEffect(() => {
    async function fetchTotalStaked() {
      try {
        setTotalStaked(await polydogeFinance.getTotalStakedInBoardroom());
      } catch (err) {
        console.error(err);
      }
    }
    if (isUnlocked) {
      fetchTotalStaked();
    }
  }, [isUnlocked, slowRefresh, polydogeFinance]);

  return totalStaked;
};

export default useTotalStakedOnBoardroom;
