import {useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import useRefresh from './useRefresh';

const useFetchBoardroomAPR = () => {
  const [apr, setApr] = useState<number>(0);
  const polydogeFinance = usePolyDogeFinance();
  const {slowRefresh} = useRefresh();

  useEffect(() => {
    async function fetchBoardroomAPR() {
      try {
        setApr(await polydogeFinance.getBoardroomAPR());
      } catch (err) {
        console.error(err);
      }
    }
    fetchBoardroomAPR();
  }, [setApr, polydogeFinance, slowRefresh]);

  return apr;
};

export default useFetchBoardroomAPR;
