import {useEffect, useState} from 'react';
import usePolyDogeFinance from '../usePolyDogeFinance';
import useRefresh from '../useRefresh';

const useWithdrawCheck = () => {
  const [canWithdraw, setCanWithdraw] = useState(false);
  const polydogeFinance = usePolyDogeFinance();
  const {slowRefresh} = useRefresh();
  const isUnlocked = polydogeFinance?.isUnlocked;

  useEffect(() => {
    async function canUserWithdraw() {
      try {
        setCanWithdraw(await polydogeFinance.canUserUnstakeFromBoardroom());
      } catch (err) {
        console.error(err);
      }
    }
    if (isUnlocked) {
      canUserWithdraw();
    }
  }, [isUnlocked, polydogeFinance, slowRefresh]);

  return canWithdraw;
};

export default useWithdrawCheck;
