import {useEffect, useState} from 'react';
import useRefresh from '../useRefresh';
import usePolyDogeFinance from '../usePolyDogeFinance';

const useClaimRewardCheck = () => {
  const {slowRefresh} = useRefresh();
  const [canClaimReward, setCanClaimReward] = useState(false);
  const polydogeFinance = usePolyDogeFinance();
  const isUnlocked = polydogeFinance?.isUnlocked;

  useEffect(() => {
    async function canUserClaimReward() {
      try {
        setCanClaimReward(await polydogeFinance.canUserClaimRewardFromBoardroom());
      } catch (err) {
        console.error(err);
      }
    }
    if (isUnlocked) {
      canUserClaimReward();
    }
  }, [isUnlocked, slowRefresh, polydogeFinance]);

  return canClaimReward;
};

export default useClaimRewardCheck;
