import {useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import usePolyDogeFinance from './usePolyDogeFinance';
import useRefresh from './useRefresh';

const useEarningsOnBoardroom = () => {
  const {slowRefresh} = useRefresh();
  const [balance, setBalance] = useState(BigNumber.from(0));
  const polydogeFinance = usePolyDogeFinance();
  const isUnlocked = polydogeFinance?.isUnlocked;

  useEffect(() => {
    async function fetchBalance() {
      try {
        setBalance(await polydogeFinance.getEarningsOnBoardroom());
      } catch (e) {
        console.error(e);
      }
    }
    if (isUnlocked) {
      fetchBalance();
    }
  }, [isUnlocked, polydogeFinance, slowRefresh]);

  return balance;
};

export default useEarningsOnBoardroom;
