import {useCallback} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useHarvestFromBoardroom = () => {
  const polydogeFinance = usePolyDogeFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleReward = useCallback(() => {
    handleTransactionReceipt(polydogeFinance.harvestCashFromBoardroom(), 'Claim PDDOLLAR from Boardroom');
  }, [polydogeFinance, handleTransactionReceipt]);

  return {onReward: handleReward};
};

export default useHarvestFromBoardroom;
