/* eslint-disable */
import React, { useMemo } from 'react';
import { useWallet } from 'use-wallet';
import moment from 'moment';
import Harvest from './components/Harvest';
import Stake from './components/Stake';
import { makeStyles } from '@material-ui/core/styles';
import config from '../../config';
import { Button, Grid } from '@material-ui/core';
import Page from '../../components/Page';
import useRedeemOnBoardroom from '../../hooks/useRedeemOnBoardroom';
import useStakedBalanceOnBoardroom from '../../hooks/useStakedBalanceOnBoardroom';
import { getDisplayBalance } from '../../utils/formatBalance';
import useCurrentEpoch from '../../hooks/useCurrentEpoch';
import useFetchBoardroomAPR from '../../hooks/useFetchBoardroomAPR';
import useCashPriceInEstimatedTWAP from '../../hooks/useCashPriceInEstimatedTWAP';
import useTreasuryAllocationTimes from '../../hooks/useTreasuryAllocationTimes';
import useTotalStakedOnBoardroom from '../../hooks/useTotalStakedOnBoardroom';
import useClaimRewardCheck from '../../hooks/boardroom/useClaimRewardCheck';
import useWithdrawCheck from '../../hooks/boardroom/useWithdrawCheck';
import ProgressCountdown from './components/ProgressCountdown';
import { createGlobalStyle } from 'styled-components';
import HomeImage from '../../assets/img/background.png';
import BoardRoomImage from '../../assets/img/polydogedao_printer.png';
import './Boardroom.css';
const BackgroundImage = createGlobalStyle`
  body {
    background-image: url(${HomeImage});
  }
`;

const useStyles = makeStyles((theme) => ({
  gridItem: {
    height: '100%',
    [theme.breakpoints.up('sm')]: {
      height: '90px',
    },
  },
}));

const Boardroom = () => {
  const classes = useStyles();
  const { account } = useWallet();
  const { onRedeem } = useRedeemOnBoardroom();
  const stakedBalance = useStakedBalanceOnBoardroom();
  const currentEpoch = useCurrentEpoch();
  const cashStat = useCashPriceInEstimatedTWAP();
  const totalStaked = useTotalStakedOnBoardroom();
  const boardroomAPR = useFetchBoardroomAPR();
  const canClaimReward = useClaimRewardCheck();
  const canWithdraw = useWithdrawCheck();
  const scalingFactor = useMemo(() => (cashStat ? Number(cashStat.priceInDollars).toFixed(4) : null), [cashStat]);
  const { to } = useTreasuryAllocationTimes();
  const pdsharesActive = Date.now() >= config.boardroomLaunchesAt.getTime();

  return (
    <Page>
      <BackgroundImage />
            <div className='subHeader'>
              <div className='containerInfoSub'>
                <img className="boardRoomImg" src={BoardRoomImage} />
                <div className='pageInfo'>
                  <h1 className='titlePage'>Boardroom</h1>
                  <span>Stake PDS to earn PDD</span>
                </div>
              </div>
              <div className="boardRoomLive" target="_blank">LIVE
              </div>
            </div>


              <Grid container spacing={2} className="tabsBoardroom">
                <Grid item xs={12} sm={3} className="gridFlex">
                  <div className='tabInfoBoardroom baseCard'>
                    <div>
                      <div className='currentEpoch'>Current Epoch:</div>
                      <span className='fontBold'>{Number(currentEpoch)}</span>
                    </div>
                    <div>
                      <div className='nextEpoch'>Next Epoch:</div>
                      <span className="countDownBoard"><ProgressCountdown base={moment().toDate()} hideBar={true} deadline={to} description="Next Epoch"/></span>
                    </div>
                  </div>
                </Grid>
                <Grid item xs={12} sm={3} className="gridFlex">
                    <div className='tabInfoBoardroom baseCard'>
                      <div>
                        <div>APR</div>
                        <span className='fontBold'>{boardroomAPR.toFixed(2)}%</span>
                    </div>
                  </div>
                </Grid>
                <Grid item xs={12} sm={3} className="gridFlex">
                  <div className='tabInfoBoardroom baseCard'>
                    <div>
                      <div className='titleTabs'>
                        Twap price (1PDD):
                      </div>
                      <span className='fontBold'>
                        {scalingFactor} MATIC
                      </span>
                    </div>
                  </div>
                </Grid>
                <Grid item xs={12} sm={3} className="gridFlex">
                  <div className='tabInfoBoardroom baseCard'>
                    <div>
                      <div className='titleTabs'>
                        PDSHARES staked:
                      </div>
                      <span className='fontBold'>
                        {getDisplayBalance(totalStaked)}
                      </span>
                    </div>
                  </div>
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <Harvest />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Stake />
                </Grid>
              </Grid>

           {/*  <div mt={5}>
              <Grid container justifyContent="center" spacing={3} mt={10}>
                <Button
                  disabled={stakedBalance.eq(0) || (!canWithdraw && !canClaimReward)}
                  onClick={onRedeem}
                  className={
                    stakedBalance.eq(0) || (!canWithdraw && !canClaimReward)
                      ? 'shinyButtonDisabledSecondary'
                      : 'shinyButtonSecondary'
                  }
                >
                  Claim &amp; Withdraw
                </Button>
              </Grid>
            </div> */}
          {/* </>
        )
      ) : (
        <UnlockWallet />
      )} */}
    </Page>
  );
};


export default Boardroom;
