import {useEffect, useState} from 'react';
import usePolyDogeFinance from '../usePolyDogeFinance';
import {AllocationTime} from '../../polydoge-finance/types';

const useUnstakeTimerBoardroom = () => {
  const [time, setTime] = useState<AllocationTime>({
    from: new Date(),
    to: new Date(),
  });
  const polydogeFinance = usePolyDogeFinance();

  useEffect(() => {
    if (polydogeFinance) {
      polydogeFinance.getUserUnstakeTime().then(setTime);
    }
  }, [polydogeFinance]);
  return time;
};

export default useUnstakeTimerBoardroom;
