import React, {useMemo} from 'react';
import {Button} from '@material-ui/core';
import TokenSymbol from '../../../components/TokenSymbol';
import Label from '../../../components/Label';
import Value from '../../../components/Value';
import useClaimRewardTimerBoardroom from '../../../hooks/boardroom/useClaimRewardTimerBoardroom';
import useClaimRewardCheck from '../../../hooks/boardroom/useClaimRewardCheck';
import ProgressCountdown from './ProgressCountdown';
import useHarvestFromBoardroom from '../../../hooks/useHarvestFromBoardroom';
import useEarningsOnBoardroom from '../../../hooks/useEarningsOnBoardroom';
import usePDDollarStats from '../../../hooks/usePDDollarStats';
import {getDisplayBalance} from '../../../utils/formatBalance';


const Harvest: React.FC = () => {
  const pddollarStats = usePDDollarStats();
  const {onReward} = useHarvestFromBoardroom();
  const earnings = useEarningsOnBoardroom();
  const canClaimReward = useClaimRewardCheck();

  const tokenPriceInDollars = useMemo(
    () => (pddollarStats ? Number(pddollarStats.priceInDollars).toFixed(2) : null),
    [pddollarStats],
  );

  const earnedInDollars = (Number(tokenPriceInDollars) * Number(getDisplayBalance(earnings))).toFixed(2);

  const {from, to} = useClaimRewardTimerBoardroom();

  return (
    <div className="mainContainer">
      <div className="baseCard harvestCard">
        <div className="label">reward</div>
        <div className="containerInfoCard">
          <TokenSymbol symbol="PDDOLLAR" />
          <div className="harvestValue">
            <Value value= {Number(getDisplayBalance(earnings)).toFixed(2)} />
          </div>
          <div className="harvestValueEarned">
            <Label text={`≈ $${earnedInDollars}`} variant="nodey" />
            <Label text="PDDOLLAR Earned" color="#d3cfd4" />
          </div>
        </div>
        <div>
          <Button
            onClick={onReward}
            className={earnings.eq(0) || !canClaimReward ? 'claimHarvestOff' : 'claimHarvestOn'}
            disabled={earnings.eq(0) || !canClaimReward}
          >
            Claim Reward
          </Button>
        </div>
        <div>
          {canClaimReward ? (
            ''
          ) : (
            <div>
              <div className="unlockReward">Claiming rewards unlocks in:</div>
              <div className="unlockRewardCounter">
                <ProgressCountdown hideBar={true} base={from} deadline={to} description="Claim available in" />
              </div>
            </div>
          )}
        </div>
      </div>
      <div className="baseCard harvestBottom">
        <p>
          Rewards are not locked.
          <br />
          {/* Each time rewards are claimed, locker counter will reset. */}
        </p>
      </div>
    </div>
  );
};


export default Harvest;
