import {useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import {AllocationTime} from '../polydoge-finance/types';
import useRefresh from './useRefresh';

const useTreasuryAllocationTimes = () => {
  const {slowRefresh} = useRefresh();
  const [time, setTime] = useState<AllocationTime>({
    from: new Date(),
    to: new Date(),
  });
  const polydogeFinance = usePolyDogeFinance();
  useEffect(() => {
    if (polydogeFinance) {
      polydogeFinance.getTreasuryNextAllocationTime().then(setTime);
    }
  }, [polydogeFinance, slowRefresh]);
  return time;
};

export default useTreasuryAllocationTimes;
